<template>
  <div class='root'>
    <action-bar :border="false"  :left="false" title='消息中心'></action-bar>
    <div class='container'>
      <scroll-view ref="scroll" class='content' refresher @pullrefresh="onRefresh">
        <!-- 订单通知 -->
        <news-item v-if="orderNotice === null ? false : true" :datas="orderNotice" @click.native="bindMsgList(orderNotice.type)" iconType="order"></news-item>
        <!--售后通知  -->
        <news-item v-if="afterSalesNotice === null ? false : true" :datas="afterSalesNotice"
          iconType="afterSales"></news-item>
        <!-- 评价通知 -->
        <news-item v-if="evaluateNotice === null ? false : true" :datas="evaluateNotice"
          iconType="evaluate"></news-item>
        <!-- 扣款通知 -->
        <news-item v-if="deductNotice === null ? false : true" :datas="deductNotice" iconType="deduct"></news-item>
        <!-- 品类过期通知 -->
        <news-item v-if="overdueNotice === null ? false : true" :datas="overdueNotice" iconType="overdue"></news-item>
        <!-- 路线截止通知 -->
        <news-item v-if="numberNotice === null ? false : true" :datas="numberNotice" iconType="number"></news-item>
        <div
          v-if="!orderNotice && !afterSalesNotice && !evaluateNotice && !deductNotice && !overdueNotice && !numberNotice"
          class="goods-empty">
          <img src="@/assets/icon_pic_goods_empty.png" class="img-empty" />
        </div>
      </scroll-view>
    </div>
  </div>
</template>
<script>
import newsItem from '@/components/news/NewsItem.vue'
import { noticeIndex } from '@/http/api'
export default {
  name: 'news',
  components: {
    newsItem
  },
  data () {
    return {
      orderNotice: null,
      afterSalesNotice: null,
      evaluateNotice: null,
      deductNotice: null,
      overdueNotice: null,
      numberNotice: null
    }
  },
  methods: {
    // 查看对应消息列表
    bindMsgList (type) {
      this.$router.push({
        name: 'newsTypeList',
        query: {
          type
        }
      })
    },
    onRefresh () {
      this.getData()
    },
    getData () {
      noticeIndex().then(res => {
        this.$refs.scroll.refreshed()
        this.orderNotice = res.data.orderNotice === undefined ? null : res.data.orderNotice
        this.afterSalesNotice = res.data.afterSalesNotice === undefined ? null : res.data.afterSalesNotice
        this.evaluateNotice = res.data.evaluateNotice === undefined ? null : res.data.evaluateNotice
        this.deductNotice = res.data.deductNotice === undefined ? null : res.data.deductNotice
        this.overdueNotice = res.data.overdueNotice === undefined ? null : res.data.overdueNotice
        this.numberNotice = res.data.numberNotice === undefined ? null : res.data.numberNotice
      }).catch(rej => {
        this.$refs.scroll.refreshed()
        this.$toast(rej.msg)
      })
    }
  },
  mounted () {
    this.getData()
  }
}
</script>
<style lang="scss" scoped>
.root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding-bottom: 3.125rem;
}

.container {
  flex-grow: 1;
  position: relative;
}

.content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  font-size: .875rem;
}

.goods-empty {
  width: 100vw;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-empty {
  width: 34vw;
  height: auto;
}
</style>
