<template>
  <div class="news">
    <div>
      <van-image :src="getIconPath()" class="news-icon" />
    </div>
    <div class="news-right">
      <div class="news-right-top">
        <div>{{ datas.type }}</div>
        <div class="news-other-font">{{ datas.createDateTime }}</div>
      </div>
      <div class="news-right-bottom">
        <div class="news-other-font news-content">{{ datas.content }}</div>
        <div v-if="datas.unReadCount === 0 ? false : true" class="news-mark">{{ datas.unReadCount }}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'newsItem',
  props: {
    iconType: {
      type: String,
      default: ''
    },
    datas: {
      type: Object,
      default: () => {
        return {
          type: '',
          unReadCount: 0,
          content: '',
          createDateTime: ''
        }
      }
    }
  },
  computed: {

  },
  data () {
    return {
    }
  },
  methods: {
    getIconPath () {
      if (this.iconType === 'order') {
        return require('@/assets/ic_message_order_.png')
      } else if (this.iconType === 'afterSales') {
        return require('@/assets/ic_message_aftersales_.png')
      } else if (this.iconType === 'evaluate') {
        return require('@/assets/ic_message_evaluation_.png')
      } else if (this.iconType === 'deduct') {
        return require('@/assets/ic_message_deduction_.png')
      } else if (this.iconType === 'overdue') {
        return require('@/assets/ic_message_overdue_.png')
      } else if (this.iconType === 'number') {
        return require('@/assets/ic_message_route_.png')
      }
    }
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>
.news-other-font {
  color: #888F9F;
}

.news-icon {
  width: 3rem;
  height: 3rem;
  margin-right: .625rem;
}

.news {
  padding: .625rem;
  display: flex;
  flex-direction: row;
}

.news-right {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 10%;
}

.news-right-top {
  display: flex;
  justify-content: space-between;
}

.news-right-bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.news-mark {
  flex-shrink: 0;
  color: white;
  min-width: .875rem;
  height: .875rem;
  font-size: .625rem;
  padding: .0625rem .1875rem;
  box-sizing: border-box;
  text-align: center;
  border-radius: 1.25rem;
  line-height: .875rem;
  background-color: #C92434;
}

.news-content {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
